import Layout from '../components/Layout';

import { useEffect, useState } from 'react'
import gql from "graphql-tag";
import { client } from '../components/Layout';

const index = () => {

    const [rockets, setRockets] = useState([])

    useEffect(() => {
        client
            .query({
                query: gql`
              query GET_ROCKETS {
                rockets {
                  id
                  name
                  description
                  country
                }
              }            
          `
            })
            .then(result => {
                console.log(result)
                setRockets(result.data.rockets)
            });
    }, [])

    return (
        <div>
            <Layout>
                <div id="img" />
                <div class="columns">
                    {rockets.map(rocket => (
                        <div class="column" style={{ margin: '30px'}}>
                            <div class="card" style={{ width: '280px'}}>
                                <div class="card-image">
                                    <figure class="image is-4by3">
                                        <img src="https://bulma.io/images/placeholders/1280x960.png" alt="Placeholder image" />
                                    </figure>
                                </div>
                                <div class="card-content">
                                    <div class="media">
                                        <div class="media-content">
                                            <p class="title is-4" style={{ fontSize:'1.5em' }}>{rocket.name}</p>
                                            <a class="subtitle is-6">@{rocket.id}</a>
                                        </div>
                                    </div>

                                    <div class="content">
                                        <p><b>Description: </b></p>
                                        {rocket.description}
                                    </div>
                                    <br/>
                                    <div class="content">
                                        <p><b>Country: </b> {rocket.country}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </Layout>

            <style jsx>{`
            #img {
                background-image: url("/home_bgi.jpg");
                background-attachment: fixed;
                background-repeat: no-repeat;
                background-size: cover;
                height: 85vh;
            }
            `}</style>
        </div >
    );
};

export default index;
